const algorithms = {
  maxTime: 50,

  decodePattern: (pattern) => {
    let split = pattern.split(';');
    return split.map((value, index) => {
      let values = value.split(",");

      return {
        memoryUsage: parseInt(values[0]),
        duration: parseInt(values[1]),
        label: String.fromCharCode(index + 65)
      }
    });
  },

  initMemory: () => {
    let memory = [];

    for (let i = 0; i < algorithms.maxTime; i++) {
      memory.push(null);
    }

    return memory;
  },

  findSuitableMemoryBlocks: (memory, process) => {
    let blocks = [];
    for (let i = 0; i < memory.length; i++) {
      let offset = 0;
      while (i + offset < memory.length && memory[i + offset] === null) {
        offset++;
      }

      if (offset >= process.memoryUsage) {
        blocks.push({start: i, length: offset});
      }

      i += offset;
    }

    return blocks;
  },

  fillMemory: (memory, process, startIndex) => {
    for (let i = startIndex; i < startIndex + process.memoryUsage; i++) {
      memory[i] = process;
    }
  },

  memoryAlgorithmWrapper: (memoryUsageAlgorithm) => {
    return (patternString) => {
      let pattern = algorithms.decodePattern(patternString);
      let memory = algorithms.initMemory();

      let endTime = pattern
        .map((value, index) => value.duration + index)
        .reduce((previousValue, currentValue) => Math.max(previousValue, currentValue), 0);

      let runningProcesses = [];
      let processHistory = [];

      for (let i = 0; i < endTime; i++) {
        for (let runningProcess of runningProcesses) {
          runningProcess.duration--;
        }

        runningProcesses = runningProcesses.filter(value => value.duration !== 0);

        for (let j = 0; j < memory.length; j++) {
          if (memory[j] !== null && memory[j].duration === 0) {
            memory[j] = null;
          }
        }

        if (i < pattern.length) {
          runningProcesses.push(pattern[i]);
          if(!memoryUsageAlgorithm(memory, pattern[i])) {
            processHistory.push(memory.map(value => value));

            processHistory.push(-1);
            return processHistory;
          }
        }

        processHistory.push(memory.map(value => value));
      }

      return processHistory;
    }
  },

  last: (patternString) => {
    let algorithm = (memory, process) => {
      let memoryBlocks = algorithms.findSuitableMemoryBlocks(memory, process);

      if (memoryBlocks.length === 0)
        return false;

      let memoryBlock = memoryBlocks[memoryBlocks.length - 1];

      algorithms.fillMemory(memory, process, memoryBlock.start);

      return true;
    };

    return algorithms.memoryAlgorithmWrapper(algorithm)(patternString);
  },

  best: (patternString) => {
    let algorithm = (memory, process) => {
      let memoryBlocks = algorithms.findSuitableMemoryBlocks(memory, process);

      if (memoryBlocks.length === 0)
        return false;


      memoryBlocks.sort((a,b) => (a.length > b.length) ? 1 : ((b.length > a.length) ? -1 : 0));
      let memoryBlock = memoryBlocks[0];

      algorithms.fillMemory(memory, process, memoryBlock.start);

      return true;
    };

    return algorithms.memoryAlgorithmWrapper(algorithm)(patternString);
  },

  worst: (patternString) => {
    let algorithm = (memory, process) => {
      let memoryBlocks = algorithms.findSuitableMemoryBlocks(memory, process);

      if (memoryBlocks.length === 0)
        return false;

      memoryBlocks.sort((a,b) => (a.length > b.length) ? -1 : ((b.length > a.length) ? 1 : 0));
      let memoryBlock = memoryBlocks[0];

      algorithms.fillMemory(memory, process, memoryBlock.start);

      return true;
    };

    return algorithms.memoryAlgorithmWrapper(algorithm)(patternString);
  },

  random: (patternString) => {
    let algorithm = (memory, process) => {
      let memoryBlocks = algorithms.findSuitableMemoryBlocks(memory, process);

      if (memoryBlocks.length === 0)
        return false;

      let memoryBlock = memoryBlocks[Math.floor(Math.random() * memoryBlocks.length)];

      algorithms.fillMemory(memory, process, memoryBlock.start);

      return true;
    };

    return algorithms.memoryAlgorithmWrapper(algorithm)(patternString);
  }
};

export default algorithms;
