<template>
  <v-app>
    <v-app-bar app>
      <v-toolbar-title class="headline">
        <span>Mäluhõivamisalgoritmid</span>
      </v-toolbar-title>
    </v-app-bar>

    <v-snackbar
      :timeout="4000"
      v-model="error"
      color="error"
      top
    >
      {{ errorMessage }}
    </v-snackbar>

    <v-content>
      <v-container fluid style="height: 100%" class="ma-0">
        <v-row dense>
          <v-col cols="2" dense>
            <v-row
              v-for="n in algorithms.length"
              :key="n"

            >
              <v-col>
                <v-card width="100%"
                        :flat="selectedAlgorithm !== n - 1"
                        :raised="selectedAlgorithm === n - 1"
                        @click="select(n)"
                >
                  <div class="pt-4 pb-4" style="text-align: center">{{algorithms[n - 1].label}}
                  </div>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
          <v-divider vertical></v-divider>
          <v-col cols="9" dense>
            <v-row dense justify="center">
              <v-col>
                <v-card>
                  <v-row dense>
                    <v-col>
                      <v-card-title class="ml-2">Vali või sisesta järjend</v-card-title>

                    </v-col>

                  </v-row>

                  <v-divider class="ml-4 mr-4"></v-divider>
                  <v-radio-group v-model="radioGroup" style="width: 100%">
                    <v-row dense
                           class="ma-2"
                           v-for="n in predefinedPatterns"
                           :key="n.label"

                    >
                      <v-col cols="4">
                        <v-radio
                          :value="n.pattern"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            {{n.label}}
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="6">
                      <span>
                        {{n.pattern}}
                      </span>
                      </v-col>
                    </v-row>
                    <v-row dense
                           class="ma-2 mb-0"

                    >
                      <v-col cols="4">
                        <v-radio
                          value="custom"
                        >
                          <template slot="label">
                          <span class="mr-12">
                            Enda oma
                          </span>

                          </template>
                        </v-radio>

                      </v-col>
                      <v-col cols="5">
                        <v-text-field class="ma-0 pa-0" v-model="customPattern"
                                      @click="radioGroup='custom'">

                        </v-text-field>
                      </v-col>
                      <v-spacer></v-spacer>
                      <v-col no-gutters class="pb-4 mr-4" style="flex-flow: 0; flex-shrink: 1">
                        <v-btn color="green" text @click="save" outlined>Käivita</v-btn>
                      </v-col>
                    </v-row>
                  </v-radio-group>
                </v-card>
              </v-col>
            </v-row>
          </v-col>

        </v-row>

        <v-row justify="center" dense>
          <v-col align="center" ref="canvasWrapper">
            <canvas ref="drawing"></canvas>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
    import algorithms from './plugins/algorithms';

    export default {
        name: 'App',
        data: () => ({
            result: undefined,
            radioGroup: undefined,

            error: false,
            errorMessage: "",

            colors: ["#206F71",
                "#8BC9AE",
                "#FCEBA9",
                "#FCB06F",
                "#D94B50",
                "#D291BC",
                "#C1E7E3",
                "#FFDCF4",
                "#AFDAC1",
                "#B399D4"],
            colorIndex: 0,

            algorithms: [{label: "Last-Fit", func: algorithms.last}, {
                label: "Best-Fit",
                func: algorithms.best
            }, {label: "Worst-Fit", func: algorithms.worst}, {
                label: "Random-Fit",
                func: algorithms.random
            }],

            customPattern: "",

            selectedAlgorithm: 0,

            predefinedPatterns: [
                {
                    label: "Esimene",
                    pattern: "1,8;35,4;3,6;4,2;1,4;3,3;1,2;5,1;50,1"
                },
                {
                    label: "Teine",
                    pattern: "4,5;2,7;9,2;4,6;7,1;6,4;8,8;3,6;1,10;9,2"
                },
                {
                    label: "Kolmas",
                    pattern: "5,10;6,6;3,9;8,4;3,6;5,12;1,4;15,3;3,4;9,7"
                },
                {
                    label: "Ekstra pikk",
                    pattern: "1,8;35,4;8,6;4,2;2,2;5,3;1,2;5,4;4,4;2,8;25,4;8,6;4,6;2,14;5,3;1,2;5,12;4,1;10,7;4,4;6,6"
                }
            ],

            labels: []
        }),

        methods: {
            save: function () {
                this.error = false;
                try {
                    let pattern;
                    if (this.radioGroup === "custom") {
                        pattern = this.customPattern;
                    } else {
                        pattern = this.radioGroup;
                    }
                    this.result = this.algorithms[this.selectedAlgorithm].func(pattern);

                    this.labels = pattern.split(";").map((value, index) => String.fromCharCode(index + 65) + " " + value);

                    console.log(this.result);
                    // console.log(this.result.map(pattern => pattern.map(p => p === null ? p : p.label)));
                } catch (e) {
                    this.error = true;
                    this.errorMessage = e;
                    return;
                }

                this.drawMemory(this.result);
            },

            drawMemory: function (processHistory) {
                let canvas = this.$refs["drawing"];
                let canvasWrapper = this.$refs["canvasWrapper"];
                canvas.width = Math.min(canvasWrapper.clientWidth - 24, 1400);


                const ctx = canvas.getContext("2d");

                let sideLen = Math.floor((canvas.width) / 55);
                ctx.fillStyle = "black";

                canvas.height = sideLen * (processHistory.length + 3);

                ctx.font = "12px Arial";
                ctx.fillText("Etapp", 0, sideLen * 2);

                ctx.fillText("Lisatud", sideLen * 2.7, sideLen * 2 - 12);
                ctx.fillText("protsess", sideLen * 2.7, sideLen * 2);

                for (let j = 0; j < algorithms.maxTime; j++) {
                    let x = sideLen * (j + 5.4);
                    let y = sideLen * 1.8;
                    ctx.fillText(j, x, y);
                }

                for (let i = 0; i < processHistory.length; i++) {

                    if (processHistory[i] === -1) {
                        this.error = true;
                        this.errorMessage = "Protsessi " + this.labels[i - (i > 0 ? 1 : 0)] + " paigutamiseks ei leidunud ruumi";
                        return;
                    }

                    let y1 = sideLen * (i + 2);

                    ctx.font = "12px Arial";
                    ctx.fillText(i + 1, sideLen, y1 + sideLen / 1.3);

                    if (i < this.labels.length) {
                      ctx.fillText(this.labels[i], sideLen * 3, y1 + sideLen / 1.3);
                    }

                    for (let j = 0; j < algorithms.maxTime; j++) {
                        let x1 = sideLen * (j + 5) + 1;
                        let x2 = sideLen + 1;
                        let y2 = sideLen + 1;


                        ctx.fillStyle = processHistory[i][j] === null ? "white" : this.colors[processHistory[i][j].label.charCodeAt(0) % this.colors.length];
                        ctx.fillRect(x1, y1, x2, y2);

                        ctx.beginPath();
                        ctx.rect(x1, y1, x2, y2);
                        ctx.stroke()

                        ctx.fillStyle = "black";
                        ctx.font = "10px Arial";
                        ctx.fillText(processHistory[i][j] === null ? "-" : processHistory[i][j].label, x1  + sideLen / 2.4, y1  + sideLen / 1.5);
                        // console.log(x1, y1, x2, y2);

                    }
                }

            },

            select: function (n) {
                this.selectedAlgorithm = n - 1;
                this.save();
            }
        },

        computed: {},

        created: function () {
            this.radioGroup = this.predefinedPatterns[0].pattern;
        },

        watch: {
            radioGroup: function () {
                this.save();
            }
        }

    };
</script>

<style>
  .v-input__control {
    width: 100% !important;
  }
</style>
